  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', sans-serif;
  width: 100%;
  color: #000;

}
.app-bar {
  padding: 8px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__brand h1 {
  color:black;
  font-size:15px;
  user-select: none;
  font-family: 'Rowdies', cursive;
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: white;
  overflow: hidden;
}

.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: black;
  padding: 8px;
  margin-bottom: 5px;
  width: 100%;
}

.nav__item a{
  font-size:18px;
  height: 50px;
  width: 50px;
}

.skip-link {
  position: absolute;
  top:-45px;
  left:0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
  min-width: 44px;
  min-height: 44px;
}

.skip-link:focus {
  top: 0;
}

.hero {
  display: flex;
  align-items: center;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size:100%;
  background-position:center;
  background-color: #000;
  background-repeat: no-repeat;
  height: 500px;
}

.hero__inner {
  margin: 0 auto;
  max-width: 80%;
}

.hero__title {
  color: #fff;
  font-family: 'Rowdies', cursive;
}

.hero__tagline {
  color: #fff;
  margin-top: 16px;
  text-shadow: 1px 1.5px black;
}

.content {
  padding: 0;
  margin-top: -2rem;
}

.content h2 {
  margin-top: 2rem;
  text-align: center;
  padding-top: 50px;
  color: black;
  font-family: 'Rowdies', cursive;


}

#resto-list{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin: 32px 50px;
}

.card-list {
  width: 100%;
  box-shadow: 2px 4px 8px 4px rgba(41, 41, 41, 0.223);
  border-radius: 8px;
  overflow: hidden;
}

.card-list:focus{
 outline: #000;
}
.card-list h3  {
  text-align: center;
  font-size: 28px;
  margin: 1rem;
}

.card-list a{
  font-size: 25px;
  padding: 10px;
  height: 44px;
}

.card-body {
  padding: 20px;
}


.card-list img {
  height: auto;
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}

.info{
  margin-top:20px;
}

.info p{
  padding:10px;
  border-radius: 10px;
  background-color: #000;
  display: inline-block;
  color: white;
  margin-right:1rem ;
}
footer {
  background-color: black;
  height: 5rem;
  margin-top: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

#hamburgerButton{
  min-height: 50px;
  min-width: 50px;
  font-size:40px;
}

/* DETAIL RESTO */
.detail-resto{
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 2fr;
  gap: 18px 16px;
  padding: 15px;
}
.detail-resto .restaurant__poster{
  max-height:380px;
  justify-content: center;
  max-width:100%;
}

table{
  max-width:100%;
}
table, th, td {
  border: 1px solid rgba(0, 0, 0, 0.527);
  border-collapse: collapse;
}

  th, td {
    padding: 15px;
  }

table th{
  background-color:grey;
  color: white;
}

.reviews{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap:1rem;
  max-width:100%;
}

div .card-review{
  background-color: #f5f5f5;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: black;
  
  position: fixed;
  bottom: 25px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  top: auto;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

