___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (width<=800px){
  .card-list {
    display: flex;
    flex-direction: column;
  }
  .img-resto {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }
    #resto-list{
      grid-template-columns:repeat(1, 1fr);
      width: 80%;
      margin-left: auto;
      margin-right: auto;
    }
    .detail-resto .restaurant__poster{
      max-width:100%;
    }
  }

  @media screen and (min-width:650px) {
    .app-bar {
      grid-template-columns: 1fr auto;
      padding: 8px 32px;
    }
   
    .app-bar .app-bar__brand h1 {
      font-size: 1.5em;
    }
   
    .app-bar .app-bar__menu {
      display: none;
    }
   
    .app-bar .app-bar__navigation {
      position: static;
      width: 100%;
    }
   
    .app-bar .app-bar__navigation ul li {
      display: inline-block;
    }
   
    .app-bar .app-bar__navigation ul li a {
      display: inline-block;
      width: 120px;
      text-align: center;
      margin: 0;
    }
  }

  @media screen and (min-width:350px) {
    .hero{
      background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    }
  .card-list {
    display: flex;
    flex-direction: column;
  }

  .img-resto {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
  }

  .open {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
  }

  .info p{
    margin-top:10px;
  }

  
}

  

